export default function (Component) {
Component.options.__docs = "<p><strong><code>FdMenuItem</code> is smart:</strong></p>\n<p><code>FdMenuItem</code> looks at what you put in it. If you have an item with just text, <code>FdMenuItem</code> will detect that and automatically wrap it with an anchor-element. So this:</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">FdMenuItem</span>&gt;</span>click me<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">FdMenuItem</span>&gt;</span></code></pre>\n<p>will yield the same <em>HTML</em> as</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">FdMenuItem</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">FdMenuLink</span>&gt;</span>click me<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">FdMenuLink</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">FdMenuItem</span>&gt;</span></code></pre>\n<p>If you put anything other than just plain text inside <code>FdMenuItem</code> then this automatism won&#39;t kick in.</p>\n"
}