export default function (Component) {
Component.options.__tip = "<p>Use the <code>menu</code>-slot in combination with <code>fd-combobox-menu</code> in order to provide a custom menu. This allows you (e.g.:) to easily apply custom attributes (<code>class</code>, <code>style</code>, …). The example below is using a custom menu in order to make it the same width as the combobox itself. A custom menu also allows you to have multiple lists.</p>\n"
}