export default function (Component) {
Component.options.__tip = "<p>There are multiple ways to show a modal.</p>\n<p><strong>Using <code>$fdModal.open(modalName)</code></strong></p>\n<p>You can use the <code>$fdModal.open(modalName)</code> to open modals from anywhere. Just give each modal a name (using the <code>name</code>-prop) and pass this to the <code>open</code>-function.</p>\n<p><strong>Manually showing the modal</strong></p>\n<p>Of course you can also manually show or close the modal by simply calling the corresponding methods in it:</p>\n<ul>\n<li><code>show(event?)</code>: shows the modal.</li>\n<li><code>close(event?)</code>: closes the modal.</li>\n</ul>\n"
}