export default function (Component) {
Component.options.__docs = "<p>A popover is made up of two parts:</p>\n<ul>\n<li><strong>Popover Control:</strong> This part triggers the popover content to be displayed. You specify the popover control by providing content for the <code>control</code>-slot.</li>\n<li><strong>Popover Content:</strong> The content is what will be displayed. The content is anchored to the control element. You specify the popover&#39;s content by providing a default slot.</li>\n</ul>\n<p>Each slot (<code>control</code> and <code>default</code>) have access to the following <em>slot-props</em>:</p>\n<ul>\n<li><code>visible</code> – <em>Boolean</em>: <code>true</code> when the popover&#39;s content is currently visible.</li>\n<li><code>show</code> – <em>Function</em>: When called, the popover&#39;s content will be made visible.</li>\n<li><code>hide</code> – <em>Function</em>: When called, the popover&#39;s content will be dismissed.</li>\n<li><code>toggle</code> – <em>Function</em>: When called, the popover&#39;s visibility will be toggled.</li>\n<li><code>placement</code> – <em>String</em>: The current popover placement.</li>\n</ul>\n"
}