export default function (Component) {
Component.options.__docs = "<p>Menus are created by using <code>FdMenu</code>. A menu contains one or more <code>FdMenuList</code>s. Lists in turn contain <code>FdMenuItem</code>s. <em>This separation allows you to put similar items in the same group and bring order to chaos</em>. Further down you will find examples that show how to use multiple lists.</p>\n<p><strong>Events</strong></p>\n<ul>\n<li><code>FdMenuItem</code> emits <code>click</code> when clicked.</li>\n<li>Both, <code>FdMenuList</code> and <code>FdMenu</code> emit <code>select</code>-events every time an item was clicked. The payload is the item itself. They act as some kind of event aggregator which is useful if you don&#39;t care about the context of the clicked items so much.</li>\n</ul>\n"
}