export default "<template>\n  <FdMenu @select=\"sayHi\">\n    <FdMenuList>\n      <FdMenuItem value=\"1\">Option 1</FdMenuItem>\n      <FdMenuItem value=\"2\">\n        Option 2\n      </FdMenuItem>\n      <FdMenuItem value=\"3\">\n        Option 3\n      </FdMenuItem>\n      <FdMenuItem value=\"4\">\n        Option 4\n      </FdMenuItem>\n    </FdMenuList>\n  </FdMenu>\n</template>\n\n<script>\nexport default {\n  methods: {\n    sayHi(item) {\n      alert(`Clicked Menu Item with value: '${item.value}'`);\n    }\n  }\n};\n</script>"