export default "<template>\n  <div>\n    <div class=\"favorite-fruit\">\n      <span class=\"favorite-fruit-label\">Selected Fruit:</span>\n      <span class=\"favorite-fruit-current\">{{ fruit || \"none\" }}</span>\n    </div>\n    <fd-combobox\n      class=\"fruit-picker\"\n      v-model=\"fruit\"\n      placeholder=\"Pick your Fruit of the day\"\n    >\n      <fd-combobox-menu class=\"fruit-picker\" v-slot=\"menu\">\n        <fd-menu-list>\n          <fd-menu-item value=\"Apple\">Apple</fd-menu-item>\n          <fd-menu-item value=\"Banana\">Banana</fd-menu-item>\n          <fd-menu-item value=\"Cherry\">Cherry</fd-menu-item>\n        </fd-menu-list>\n      </fd-combobox-menu>\n    </fd-combobox>\n  </div>\n</template>\n\n<script>\nexport default {\n  methods: {\n    select(value) {\n      this.fruit = value;\n    }\n  },\n  data() {\n    return {\n      fruit: null\n    };\n  }\n};\n</script>\n\n<style scoped>\n.fruit-picker {\n  width: 300px;\n}\n\n.favorite-fruit {\n  font-size: 20px;\n  margin-bottom: 30px;\n  border-bottom: 1px solid #efefef;\n  padding-bottom: 10px;\n}\n\n.favorite-fruit-label {\n  margin-right: 10px;\n}\n\n.favorite-fruit-current {\n  font-weight: bold;\n  color: rgb(55, 160, 6);\n  background-color: #efefef;\n  padding-left: 8px;\n  padding-right: 8px;\n  padding-top: 5px;\n  padding-bottom: 5px;\n  border-radius: 5px;\n}\n</style>"